<template>
	<div class="main-container">
		<h3 class="section-title"><em>{{ experiment.title }}</em> data</h3>
		<b-form-checkbox
			ref = "enabledInput"
			v-model="enabled"
			switch
			size="lg"
			class="mt-2"
			@change="toggleToolbar"
			>
			Enable edition mode
		</b-form-checkbox>

		<b-container class="p-0">
			<b-row>
				<b-col cols=10>
					<form>
						<b-form-input ref="titleInput" id="titleInput" v-model="sheet.title" placeholder="Textsheet Title" :disabled="!isNew" class="text-center">
						</b-form-input>
					</form>
				</b-col>

				<b-col>
					<b-button @click="saveContent" class="float-right" size="lg" variant="info" style="border-radius: 15px" :disabled="saving">
						<span v-if="!saving"><b-icon icon="cloud-upload" ></b-icon> Save</span>
						<b-spinner type="grow" label="Spinning" v-if="saving"></b-spinner>
					</b-button>
				</b-col>
			</b-row>
		</b-container>

		<b-container style="background-color: #fff; color: black; overflow: hidden; border-radius: 0px 0px 0px 15px" class="p-4">
			<vue-editor v-model="sheet.content" :editorToolbar="customToolbar" :disabled="!enabled" style="height: 1000px;"></vue-editor>
		</b-container>
	</div>
</template>

<style scoped>
	#titleInput {
		border-style: none; 
		border-radius: 15px 15px 0px 0px; 
		background-color: rgba(264, 264, 264, 0.2);
		font-size: 1.8em;
		letter-spacing: 1px;
		color: rgba(264, 264, 264, 1);

	}

	#titleInput:focus {
		font-style: italic;
		color: rgba(264, 264, 264, 0.8);
	}
</style>

<script>
	import { VueEditor } from 'vue2-editor';
	import Textsheet from '../models/textsheet'
	import DateFormat from '../utils/DateFormat.js'
	import { mapState } from 'vuex'

	export default {
		components: {
			VueEditor
		},

		data() {
			return {
				sheet: undefined,
				isNew: true,
				enabled: true,
				customToolbar: [
					[{ 'size': ['small', false, 'large', 'huge'] }],
					['bold', 'italic', 'underline', 'strike'],
					[{ 'color': [] }, { 'background': [] }],
					['blockquote', { 'script': 'sub'}, { 'script': 'super' }],
					[{ 'list': 'ordered'}, { 'list': 'bullet' }, {'list': 'check'}],
					[{ 'align': [] }],
					// [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
					// [{ 'font': [] }],
					['image', 'link']
				],
				saving: false
			}
		},
		computed: {
			...mapState('user', ['username']),
			experiment() {
				return this.$store.getters['experiment/getExperimentByTitle'](this.$route.params.experimentTitle)
			}
		},
		methods: {
			getSheet() {
				const sheetTitle = this.$route.params.sheetTitle
				if (sheetTitle === 'new-entry') {
					this.sheet = new Textsheet(this.username)
				}
				else {
					try{
						console.log(this.experiment)
						this.sheet = this.experiment.data.textsheets.find(textsheet => textsheet.title === this.$route.params.sheetTitle)
						this.isNew = false
						this.enabled = false
					} catch(error) {
						console.error(error)
						alert('An error occured during loading.')
					}
				}	
			},

			// handleImageAdded(file, Editor, cursorLocation, resetUploader) {
			// 	var formData = new FormData();
			// 	formData.append('image', file)

			// 	axios({
			// 		url: 'https://fakeapi.yoursite.com/images',
			// 		method: 'POST',
			// 		data: formData
			// 	})
			// 	.then(result => {
			// 		console.log(result.data)
			// 		let url = result.data.url // Get url from response
			// 		Editor.insertEmbed(cursorLocation, 'image', url);
			// 		resetUploader();
			// 	})
			// 	.catch(err => {
			// 		console.log(err);
			// 	})
			// },

			saveContent() {
				this.saving = true
				if (this.isNew) {
					this.sheet.lastUser = this.username
					this.sheet.LastModifiedDate = DateFormat.dateString()
					try {
						if (this.sheet.title === "") {
								throw 'Please choose a title for your texsheet.'
							}
						for (var textsheet of this.experiment.data.textsheets) {
							if (textsheet.title === this.sheet.title) {
								throw 'A textsheet with this title already exists. Please choose another title.'
							}
						}

						this.experiment.data.textsheets.push(this.sheet)
						setTimeout(() => { this.saving = false }, 500)
						console.log('saved')
						console.log(this.$store.getters['experiment/getExperimentByTitle'](this.$route.params.experimentTitle).data.textsheets)
						this.$router.push('/experiments/'+this.experiment.title+'/data/textsheets/'+this.sheet.title)
						this.isNew = false
					} 
					catch(error) {
						alert(error)
					}
					finally {
						this.saving = false
					}
				}
				else {
					this.sheet.lastUser = this.username
					this.sheet.LastModifiedDate = DateFormat.dateString()
					setTimeout(() => {this.saving = false}, 500)
					console.log('saved')
					console.log(this.$store.getters['experiment/getExperimentByTitle'](this.$route.params.experimentTitle).data.textsheets)
				}	
			},
			toggleToolbar(checked) {
				const toolbar = document.getElementsByClassName('ql-toolbar')[0]
				if (checked) toolbar.classList.remove('hidden')
				else toolbar.classList.add('hidden')
			},
			initToolbar() {
				if (!this.$refs['enabledInput'].checked) {
					const toolbar = document.getElementsByClassName('ql-toolbar')[0]
					toolbar.classList.add('hidden')
				}
			}
		},

		created() {
			this.getSheet()
		},
		mounted() {
			this.initToolbar()
			this.$refs['titleInput'].focus()
		}
	}
</script>

<style>
	.ql-toolbar.hidden {
		display: none
	}
</style>
